import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay, Controller } from 'swiper/modules';
import 'swiper/css';
import { useRef, useState, useEffect } from 'react';
import { Link } from "react-router-dom";


function InfoTotal () {
    //
    const infoListTotal = [
        {"type":"",
        "title":"  PC 2 .   PC 2 .",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "url":"/board/biddingView"},
        {"type":"",
        "title":"  PC 2 .   PC 2 .",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "url":"/board/biddingView"},
        {"type":"",
        "title":"  PC 2 .   PC 2 .",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "url":"/board/careersView"},
        {"type":"",
        "title":"  PC 2 .   PC 2 .",
        "msg":"() 2025     .",
        "date":"2025-02-26",
        "url":"/board/biddingView"},
        {"type":"",
        "title":"  PC 2 .   PC 2 .",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "url":"/board/careersView"},
    ];

    return (
        <div>
            <Swiper
                className="mainTabSwiper"
                slidesPerView={"auto"}>
                {infoListTotal.slice(0, 4).map((item, index) => (
                    <SwiperSlide key={index}>
                        <i className="eyebrow">{item.type}</i>
                        <Link to={item.url} className="link">
                            <dl>
                                <dt>{item.title}</dt>
                                <dd>{item.msg}</dd>
                            </dl>
                        </Link>
                        <i className="date">{item.date}</i>
                    </SwiperSlide>
                ))}
            </Swiper>
        </div>
    );
}

