function CptMain () {
    // --------------------------- data ---------------------------
    //
    const noticeTitle = "";
    const noticeUrl = "/board/pr";
    const noticeList = [
        {"title":"     ,         ,   ",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "imgUrl":"/resource/images/main/main_thum01.jpg",
        "imgAlt":" ",
        "url":"/board/prView"},
        {"title":"     ,         ,   ",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "imgUrl":"/resource/images/main/main_thum02.jpg",
        "imgAlt":" ",
        "url":"/board/prView"},
        {"title":"     ,         ,   ",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "imgUrl":"/resource/images/main/main_thum03.jpg",
        "imgAlt":" ",
        "url":"/board/prView"},
        {"title":"     ,         ,   ",
        "msg":"  PC 2 .  PC 2    PC 2 .",
        "date":"2025-02-26",
        "imgUrl":"/resource/images/main/main_thum04.jpg",
        "imgAlt":" ",
        "url":"/board/prView"},
    ];

    // --------------------------- Main Visual ---------------------------
    const visualSwiperRef = useRef(null);
    const visualSwiperMRef = useRef(null);
    useEffect(() => {
        if (visualSwiperRef.current && visualSwiperMRef.current) {
            //   
            visualSwiperRef.current.controller.control = visualSwiperMRef.current;
            visualSwiperMRef.current.controller.control = visualSwiperRef.current;
        }
    }, [visualSwiperRef, visualSwiperMRef]);
    const progressLineRef = useRef(null); // progressLine   useRef 

    // --------------------------- / ---------------------------
    const [activeTab, setActiveTab] = useState(0);

    return (
        <div className="cptMain">
            <div className="mainArea">
                <div className="visualGroup">
                    <div className="visualSwiperContainer">
                        <Swiper 
                            className="visualSwiperM" 
                            loop={true}
                            modules={[Controller]}
                            onSwiper={(swiper) => {
                                visualSwiperMRef.current = swiper;
                            }}
                        >
                            <SwiperSlide>
                                <div className="link">
                                    <dl className="msg">
                                        <dt> <br /> </dt>
                                        <dd>   <br />    </dd>
                                    </dl>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide>
                                <div className="link">
                                    <dl className="msg">
                                        <dt>  <br />   </dt>
                                        <dd>   <br />     </dd>
                                    </dl>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide>
                                <div className="link">
                                    <dl className="msg">
                                        <dt>  <br /> </dt>
                                        <dd>   <br />    </dd>
                                    </dl>
                                </div>
                            </SwiperSlide>
                        </Swiper>
                        <div className="slideUnit">
                            <div className="autoplay-progress">
                                <svg ref={progressLineRef} viewBox="0 0 120 10">
                                    <line x1="0" y1="0" x2="120" y2="0"></line>
                                </svg>
                            </div>
                            <div className="swiper-pagination"></div>
                            <div className="swiperNavi">
                                <div className="swiper-button-prev"></div>
                                <div className="swiper-button-next"></div>
                            </div>
                        </div>
                        <Swiper
                            className="visualSwiper"
                            modules={[Navigation, Pagination, Autoplay, Controller]}
                            loop={true}
                            autoplay={{
                                delay: 6000,
                                disableOnInteraction: false
                            }}
                            pagination={{
                                el: '.visualSwiperContainer .swiper-pagination',
                                type: "fraction",
                                clickable: true,
                            }}
                            navigation={{
                                nextEl: ".visualSwiperContainer .swiper-button-next",
                                prevEl: ".visualSwiperContainer .swiper-button-prev"
                            }}
                            onSwiper={(swiper) => {
                                visualSwiperRef.current = swiper;
                            }}
                            onAutoplayTimeLeft={(swiper, time, progress) => {
                                if (progressLineRef.current) {
                                    progressLineRef.current.style.setProperty('--progress', 1 - progress);
                                }
                            }}
                        >
                            <SwiperSlide>
                                <div className="link">
                                    <dl className="msg">
                                        <dt>  </dt>
                                        <dd>       </dd>
                                    </dl>
                                    <div className="image">
                                        <img src="/resource/images/main/main_visual01.jpg" 
                                            srcSet="/resource/images/main/main_visual01_m.jpg 768w, /resource/images/main/main_visual01.jpg 800w" 
                                            sizes="(max-width: 768px) 768w, 800w" 
                                            alt="" />
                                    </div>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide>
                                <div className="link">
                                    <dl className="msg">
                                        <dt>     </dt>
                                        <dd>        </dd>
                                    </dl>
                                    <div className="image">
                                        <img src="/resource/images/main/main_visual02.jpg" 
                                            srcSet="/resource/images/main/main_visual02_m.jpg 768w, /resource/images/main/main_visual02.jpg 800w" 
                                            sizes="(max-width: 768px) 768w, 800w" 
                                            alt="" />
                                    </div>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide>
                                <div className="link">
                                    <dl className="msg">
                                        <dt>   </dt>
                                        <dd>       </dd>
                                    </dl>
                                    <div className="image">
                                        <img src="/resource/images/main/main_visual03.jpg" 
                                            srcSet="/resource/images/main/main_visual03_m.jpg 768w, /resource/images/main/main_visual03.jpg 800w" 
                                            sizes="(max-width: 768px) 768w, 800w" 
                                            alt="" />
                                    </div>
                                </div>
                            </SwiperSlide>
                        </Swiper>
                    </div>
                </div>
                <div className="contGroup">
                    <section className="noticeArea">
                        <div className="title">
                            <h3>{noticeTitle}</h3>
                            <Link to={noticeUrl} className="more"></Link>
                        </div>
                        <ul className="noticeList">
                            {noticeList.map((item, index) => (
                                <li className="item" key={index}>
                                    <Link to={item["url"]} className="link">
                                        <div className="image">
                                            <img src={item["imgUrl"]} alt={item.imgAlt} />
                                        </div>
                                        <div className="text">
                                            <span className="tit">{item.title}</span>
                                            <span className="msg">{item.msg}</span>
                                            <span className="date">{item.date}</span>
                                        </div>
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    </section>

                    <section className="infoArea">
                        <div className="infoBox infoSwiper">
                            <dl className="title">
                                <dt><h3>     </h3></dt>
                                <dd>         .</dd>
                            </dl>
                            <Swiper
                                className="infoList"
                                slidesPerView={"auto"}>
                                <SwiperSlide>
                                    <div className="card">
                                        <i className="eyebrow">Business</i>
                                        <dl>
                                            <dt><br />?</dt>
                                            <dd>   .</dd>
                                        </dl>
                                        <Link to="#" className="link"></Link>
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide>
                                    <div className="card">
                                        <i className="eyebrow">Organization</i>
                                        <dl>
                                            <dt> <br /> </dt>
                                            <dd>       .</dd>
                                        </dl>
                                        <Link to="#" className="link"></Link>
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide>
                                    <div className="card">
                                        <i className="eyebrow">Toto</i>
                                        <dl>
                                            <dt> <br /> .</dt>
                                            <dd>      .</dd>
                                        </dl>
                                        <Link to="#" className="link"></Link>
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide>
                                    <div className="card">
                                        <i className="eyebrow">Proto</i>
                                        <dl>
                                            <dt> <br /> .</dt>
                                            <dd>       .</dd>
                                        </dl>
                                        <Link to="#" className="link"></Link>
                                    </div>
                                </SwiperSlide>
                            </Swiper>
                        </div>
                    </section>

                    <section className="tabArea">
                        <div className="tabMenuGroup">
                            <ul className="tabMenu">
                                {["", "", ""].map((item, index) => (
                                    <li className="tabItem" key={index}>
                                        <button 
                                            className={`tabName ${activeTab === index ? 'on' : ''}`}
                                            title=""
                                            onClick={() => setActiveTab(index)}
                                        >{item}</button>
                                    </li>
                                ))}
                            </ul>
                            <Link to="/board/careers" className="more"></Link>
                        </div>
                        <div className="tabContianer">
                            {activeTab === 0 ? <InfoTotal /> : activeTab === 1 ? <CareersList /> : <BiddingList />}
                        </div>
                    </section>

                </div>
            </div>
        </div>
    );
}

export default CptMain;