import { Link } from "react-router-dom";

function CptHeader() {
    const menuDep1Name = ["","","",""];
    const menuDep1Arr = ["/company/company","/business/business1","/board/careers","/board/pr"];
    const menuDep2Name = [
        ["","CEO",""],
        ["","",""],
        ["",""],
        [""]];
    const menuDep2Arr = [
        ["/company/company","/company/ceo","/company/organization"],
        ["/business/business1","/business/business2","/business/business3"],
        ["/board/careers","/board/bidding"],
        ["/board/pr"]];
    return (
        <header className="cptHeader">
            <div className="headerArea">
                <div className="logoArea">
                    <Link to="/"><img src="/resource/images/main/toto_logo.svg" alt="  ()" /></Link>
                </div>
                <div className="gnbArea">
                    <ul className="depth1Group">
                        {menuDep1Name.map((item, index) => (
                            <li className="depth1Menu" key={index}>
                                <Link to={menuDep1Arr[index]} className="depth1Name">{item}</Link>
                                <ul className="depth2Group">
                                    {menuDep2Name[index].map((subItem, subIndex) => (
                                        <li className="depth2Menu" key={subIndex}>
                                            <Link to={menuDep2Arr[index][subIndex]} className="depth2Name">{subItem}</Link>
                                        </li>
                                    ))}
                                </ul>
                            </li>
                        ))}
                    </ul>
                </div>
                <div className="quickArea">
                    <a href="https://www.sportstoto.co.kr/" target="_blank" rel='noreferrer' title="  " className="quick01"></a>
                    <a href="https://www.betman.co.kr/" target="_blank" rel='noreferrer' title="  " className="quick02">betman</a>
                </div>
                <div className="allMenu">
                    <button type="button" className="btnAllMenu"></button>
                </div>
                <div className="allMenuArea">
                    <div className="menuBox">
                        <div className="logo"><img src="/resource/images/main/toto_logo.svg" alt="  ()" /></div>
                        <ul className="depth1Group">
                            {menuDep1Name.map((item, index) => (
                                <li className="depth1Menu" key={index}>
                                    <Link to={menuDep1Arr[index]} className="depth1Name">{item}</Link>
                                    <ul className="depth2Group">
                                        {menuDep2Name[index].map((subItem, subIndex) => (
                                            <li className="depth2Menu" key={subIndex}>
                                                <Link to={menuDep2Arr[index][subIndex]} className="depth2Name">{subItem}</Link>
                                            </li>
                                        ))}
                                    </ul>
                                </li>
                            ))}
                        </ul>
                        <button type="button" className="btnClose"></button>
                    </div>
                </div>
            </div>
        </header>
    );
}
export default CptHeader;