import { Link } from "react-router-dom";

function CptFooter() {
    return (
        <div className="cptFooter">
            <div className="footerArea">
                <div className="logoArea">
                    <div className="logoBox">
                        <img src="/resource/images/main/toto_logo_ft.svg" alt="()  " />
                    </div>
                    <ul className="quickBox">
                        <li className="menuItem">
                            <Link to="/legal/legal" className="menuName"></Link>
                        </li>
                    </ul>
                </div>
                <div className="copyArea">
                    <div className="copyGroup">
                        <div className="infoBox">
                            <span>()  </span><br />
                            <span>:    88-2</span><span>: </span><br />
                            <span>: 123-45-67890</span><span>: 1588-4900</span>
                        </div>
                        <div className="copyright">COPYRIGHT Sports Korea Leisure CO.,LTD ALL RIGHT RESERVED</div>
                    </div>
                    <div className="bnGroup">
                        <img src="/resource/images/main/ft_wla.png" alt="WLA" />
                    </div>
                </div>
            </div>
        </div>
    );
}
export default CptFooter;